/* global EUI */

import { definition } from '@eui/component';
import { Panel } from '@eui/panel';
import { html } from '@eui/lit-component';
import style from './menuPanel.css';
import './navigation/Navigation';

@definition('eui-menu-panel', {
  style,
  props: {
    _menuData: { type: Array },
  },
})
export default class MenuPanel extends Panel {
  /**
   * Handle navigation:select event and navigate to corresponding app
   *
   * @param {Event} event - eui:navigation event
   */
  handleEvent(event) {
    if (event.type === 'eui:navigation') {
      const { path, url } = event.detail.data;
      if (url && event.detail.type === 'external') {
        window.open(url);
      } else if (path) {
        EUI.Router.goto(path);
      }
    }
  }

  /**
   * Updates the menu strings
   * @function _updateMenuStrings
   * @description Updates the menu strings
   * @param {object} configData - Menu config data
   */
  _updateMenuStrings(configData) {
    const { MENU } = EUI.Localizer.loc;
    if (!MENU) {
      return;
    }

    function _updateLabels(data) {
      data.forEach((item) => {
        item.label = MENU[item.id] || item.label;

        if (!item.children) {
          return;
        }

        _updateLabels(item.children);
      });
    }

    _updateLabels(configData);
  }

  /**
   * @protected
   * @function didConnect
   * @description initialize the config load when component connect first time
   */
  didConnect() {
    this.state = this.storeConnect(['locale', 'activeAppPath']);
    this._menuData = EUI.MenuConfig.app.menu.data;
    this._updateMenuStrings(this._menuData);
    this._menuData = [...this._menuData];
  }

  /**
   * @protected
   * @function didChangeProps
   * @description Checks if locale has changed
   */
  didChangeProps(changedProps) {
    if (changedProps.has('state') && this.state.locale !== this.locale) {
        this._updateMenuStrings(this._menuData);
        this._menuData = [...this._menuData];
    }
  }

  /**
   * @protected
   * @function render
   * @description The render method for the component
   */
  render() {
    return html`
      <eui-navigation
        @eui:navigation="${this}"
        .data=${this._menuData}
        .activePath=${this.state.activeAppPath}>
      </eui-navigation>
    `;
  }
}
