/**
 * Declaratively create component
 * @example
 * <eui-navigation></eui-navigation>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import {
  LitComponent, html, classMap, nothing,
} from '@eui/lit-component';
import '@eui/theme';
import '@eui/base';
import style from './navigation.css';

/**
 * @property {Array} data - data for rendering navigation tree.
 * @property {String} activePath - path of the current selected/activated app
 * @property {String} isUpdating - state for triggering updates, to make tree driven by data
 */
@definition('eui-navigation', {
  style,
  props: {
    data: { type: Array },
    activePath: { type: String },
    isUpdating: { type: Boolean },
  },
})
export default class Navigation extends LitComponent {
  /**
   * Constructor for initializing property(s)
   */
  constructor() {
    super();
    // Flag for checking if it's needed to
    // initialize tree's status according to currently loaded app.
    this.needInitializing = true;
  }

  /**
   * Reset the selecting status of all the tree items
   */
  deselectAll() {
    /* eslint-disable-next-line no-return-assign */
    this._recursivelyOperateTreeLikeData(this.data, item => (item.selected = false));
  }

  /**
   * Recursively apply the given operation on tree structured data.
   *
   * @param {Object} data tree like structured data
   * @param {Function} fn Command pattern: custom function needs to be executed while looping
   */
  _recursivelyOperateTreeLikeData(data, fn) {
    for (const item of data) {
      if (item.children) {
        this._recursivelyOperateTreeLikeData(item.children, fn);
      }
      fn(item);
    }
  }

  /**
   * Handle click event on chevron
   * Expanding child tree
   * @param {Event} event click event on chevron
   * @param {Object} itemData tree node data
   */
  clickChevron(event, itemData) {
    itemData.open = !itemData.open;
    this.isUpdating = !this.isUpdating;
    event.stopPropagation();
  }

  /**
   * Handle click event on tree item
   * @param {Event} event click event for clicking tree item
   * @param {Object} itemData tree node data
   * @param {String} linkType string to see if the type of link,
   * if a the string is 'brokenLink'/'brokenConfig' the event will
   * not get dispatched
   */
  clickNode(event, itemData, linkType) {
    if (this._blockNavigationEvent(event, linkType)) {
      return;
    }
    if (itemData.type === 'group') {
      this.clickChevron(event, itemData);
    } else {
      this.deselectAll();
      itemData.selected = true;
      this.bubble('eui:navigation', itemData);
      this.isUpdating = !this.isUpdating;
      event.stopPropagation();
    }
  }

  /**
   * @description - internal function for checking details of event and linkType before
   * proceeding to dispatch 'eui:navigation' event
   * @private
   * @param {Event} event click event for clicking tree item
   * @param {String} linkType string detailing type of link
   */
  _blockNavigationEvent(event, linkType) {
    if ((event.target.tagName === 'A' && (event.ctrlKey || linkType === 'external'))
      || (linkType === 'brokenLink' || linkType === 'brokenConfig')) {
        return true;
    }
    return false;
  }

  /**
   * Generate padding style for nested tree item.
   * @param {Number} level tree node level
   * @param {Boolean} isLeaf indicate it's leaf node or child tree
   */
  getTreeItemAdditionalStyle(level, isLeaf) {
    // The chevron takes 29px
    // need to add into count to keep layout same as previous tree implementation
    return isLeaf ? `padding-left: calc(${level}em + 29px);` : `padding-left: ${level}em`;
  }

  /**
   * Generate corresponding style for tree item
   * @param {Object} itemData data for each tree item
   */
  getTreeNodeClass(itemData) {
    return classMap({
      'tree-node': true,
      'tree-node-selected': itemData.selected,
      'tree-node-open': itemData.open,
    });
  }

  /**
   * Initialize the state of navigation tree
   * according to currently loaded app
   */
  initializeTreeState() {
    if (this.needInitializing && this.activePath) {
      this._recursivelyOperateTreeLikeData(this.data, (item) => {
        item.selected = item.data && item.data.path === this.activePath;
        if (item.children) {
          item.open = item.children.some(childItem => childItem.selected || childItem.open);
        }
      });
      this.needInitializing = false;
    }
  }

  /**
   * @description - internal function for checking that the configuration set in
   * the menu.config.json is correct
   * @private
   * @param {Object} itemData data for each tree item
   */
  _validateTreeConfig(itemData) {
    let validStatus;
    if (!itemData.id || !itemData.label) {
      validStatus = 'brokenConfig';
    } else if (itemData.type === 'group') {
      validStatus = 'group';
    } else if (!itemData.data || Object.keys(itemData.data).length === 0) {
      validStatus = 'brokenLink';
    } else if (itemData.type === 'external') {
      if (itemData.data.url) {
        validStatus = 'external';
      } else {
        validStatus = 'brokenLink';
      }
    } else if (itemData.data.path) {
      validStatus = 'app';
    } else {
      validStatus = 'brokenLink';
    }
    return validStatus;
  }

  /**
   * Generate all the tree items
   */
  generateTreeItems() {
    return (this.data || []).map(dataItem => this._generateTreeItem(dataItem, 0));
  }

  /**
   * Generate tree item, it could be leaf node or child tree according to the children property
   * @param {Object} itemData data for each tree item
   * @param {Number} level tree node level
   */
  _generateTreeItem(itemData, level) {
    const linkType = this._validateTreeConfig(itemData);
    let nodeContent;
    switch (linkType) {
      case 'brokenConfig':
        nodeContent = html`Broken Menu Item`;
        console.warn('Current Menu Item is not configured correctly');
        break;
      case 'group':
        nodeContent = html`${itemData.label}`;
        break;
      case 'brokenLink':
        nodeContent = html`${itemData.label}`;
        console.warn(`Menu Item ${itemData.id} is not a valid link`);
        break;
      case 'external':
        nodeContent = html`<a href="${itemData.data.url}" target="__blank">${itemData.label}</a>`;
        break;
      default:
        nodeContent = html`<a href="#${itemData.data.path}">${itemData.label}</a>`;
    }

    if (itemData.children) {
      return html`
        <li
          class=${this.getTreeNodeClass(itemData)}
          @click=${event => this.clickNode(event, itemData, linkType)}
        >
          <span class="tree-item-title" style=${this.getTreeItemAdditionalStyle(level)}>
            <eui-v0-icon
              id="tree-chevron"
              @click=${event => this.clickChevron(event, itemData)}
              class="icon"
              name=${itemData.open ? 'chevron-down' : 'chevron-right'}
              size="14px"
            >
            </eui-v0-icon>
            ${nodeContent}
          </span>
          <ul class="tree-child-tree">
            ${itemData.children.map(item => this._generateTreeItem(item, level + 1))}
          </ul>
        </li>
      `;
    }
    return html`
      <li
        class=${this.getTreeNodeClass(itemData)}
        @click=${event => this.clickNode(event, itemData, linkType)}
      >
        <span class="tree-item-title" style=${this.getTreeItemAdditionalStyle(level, true)}>
          ${nodeContent}
          ${itemData.type === 'external' ? html`<eui-v0-icon id="external-launch-icon" name="launch"></eui-v0-icon>` : nothing}
        </span>
      </li>
    `;
  }

  /**
   * Lifecycle method gets called when property changed
   * Set the status of navigation tree intelligently
   *
   * @param {Map} changedProps changed properties
   */
  didChangeProps(changedProps) {
    this._recursivelyOperateTreeLikeData(this.data, (item) => {
      item.selected = item.data && item.data.path === this.activePath;
      if (!(changedProps.size === 1 && changedProps.has('isUpdating'))) {
        item.hasChildSelected = item.children
          && item.children.some(childItem => childItem.selected || childItem.hasChildSelected);
        item.open = item.open || item.hasChildSelected;
      }
    });
  }

  /**
   * Render the <eui-navigation> component.
   * This function is called each time a prop changes.
   */
  render() {
    this.initializeTreeState();
    const treeItems = this.generateTreeItems();
    return html`
      <div class="tree-holder">
        <ul class="tree">
          ${treeItems}
        </ul>
      </div>
    `;
  }
}

/**
 * Register the component as eui-navigation.
 * Registration can be done at a later time and with a different name
 */
Navigation.register();
